import { types } from "./actions";
import { combineReducers } from 'redux';

export const buildControllerReducer=(controller) => {
    return (state={controller:controller}, action) => {
        if(!action.controller || action.controller!==controller)
            return state;
        const storageKey = getStorageKey(
            action.id !== undefined ? action.id : 
                action.entity ? action.entity.id : undefined);
        let newStorageEntry={};
        switch (action.type) {
            case types.SHAPE_LOAD:
                return {...state, shape: {...action.entity}}
            case types.ENTITY_LOAD:
                if(Array.isArray(action.entity)){
                    action.entity.forEach(element => {
                        let elementStorageKey = getStorageKey(element.id);
                        newStorageEntry[elementStorageKey]={
                            stakeholders: 0,
                            ...state[elementStorageKey],
                            payload: {...element}
                        }
                    });
                } else {
                    newStorageEntry[storageKey]={
                        stakeholders: 0,
                        ...state[storageKey],
                        payload: {...action.entity}
                    }
                }
                return {...state, ...newStorageEntry}
            case types.ENTITY_REQUEST:
                const storedData = state[storageKey];
                const updateStakeholders = storedData ? storedData.stakeholders+1 : 1
                newStorageEntry[storageKey]={
                    ...state[storageKey],
                    stakeholders: updateStakeholders
                }
                return {...state, ...newStorageEntry}
            default:
                break;
        }
        return state;
    }
}

export function getStorageKey(id) {
    if (id===undefined)
        return undefined;
    return `id_${id}`;
}

const contextReducer = (state = {}, action) => {                                                                                                                  
    switch (action.type) {
            default:
                    return state
    }
}

export const reducersMap ={
    context: contextReducer,
    controller: (state={})=>state
}

export default combineReducers(reducersMap);
