import React, { useState } from 'react'
import ArenaClasses from './ArenaClasses.js';

export const ArenaFieldMode = {
    EDIT: 'EDIT', VIEW: 'VIEW', CREATE: 'CREATE'
}

const ArenaField = ({ value, name, parentController, type, superType, parentType, level, mode, field, update }) => {
    if (level < 0) return null;
    if (name === 'reference' || name === 'id') return null;
    if (mode === ArenaFieldMode.EDIT && !field.editable) return null;
    const contentClasses = getClassName(
        'arena-field-',
        name,
        parentController,
        type,
        superType,
        parentType,
        level,
        mode
    )

    switch (mode) {
        case ArenaFieldMode.VIEW:
            return (
                <div className={contentClasses}>
                    <ArenaViewField value={value} />
                </div>
            )
        case ArenaFieldMode.CREATE:
        case ArenaFieldMode.EDIT:
            return (
                <div className={contentClasses}>
                    <ArenaInputField value={value} onBlur={update} />
                </div>
            )
        default:
            return null;
    }

}

export default ArenaField;

export const ArenaViewField = ({ value }) => {
    return (
        <span className={ArenaClasses.FIELD_VALUE}>{value}</span>
    )
}

export const ArenaInputField = ({ value, onFocus, onBlur, type, name, options }) => {

    const [currentValue, setValue] = useState(value);

    const _onBlur = (event) => { onBlur(event.target.value) }

    const _onChange = (event) => {
        const eventValue = event.target.value;
        setValue(eventValue);
    }

    const commonProps = {
        id: `${type}.${name}`,
        className: `arena-edit-field`,
        value: currentValue,
        onChange: _onChange,
        onFocus: onFocus,
        onBlur: _onBlur,
        //placeholder: componentMapper.t(`${parentController}.${name}.placeholder`)

    }
    switch (type) {
        case 'enum':
            return buildSelect(commonProps, options)
        default:
            return (<input className='arena-edit-field' type="text" {...commonProps} />)
    }
}

const buildSelect = (commonProps, options) => {
    return (
        <select {...commonProps} style={{ height: 'unset' }}>
            <option value={undefined} disabled></option>
            {
                options.map(
                    option => (
                        <option key={option.value} value={option.value}>{option.text}</option>
                    )
                )
            }
        </select>
    );
}

const getClassName = (prefix, name, parentController, type, superType, parentType, level, mode, extraClass = "") => {
    return `${prefix}name-${name} ${prefix}parentController-${parentController} ${prefix}type-${type} ${prefix}superType-${superType} ${prefix}parentType-${parentType} ${prefix}level-${level} ${prefix}mode-${mode} ${extraClass}`;
}
