import React, {useState, useEffect, useMemo} from 'react';
import {findEntities} from './api';
import ArenaContainer from './ArenaContainer';
import {useDispatch} from 'react-redux'
import { createEntityLoad } from './redux/actions';
import { createListItemSelectionFunction } from './hooks/useSelectionFunction';
import { ArenaContainerMode } from './enums';

/**
 * 
 * @param {object} props
 * @param {string} props.controller controlador de backend para buscar las entidades
 * @param {number} props.level nivel a utilizar
 * @param {ArenaListContainerMode} props.mode modo de renderizado
 * @param {import('./ArenaContainer').updateCallback} props.onSelectFinish función de actualización de la referencia (id) de este container
 * @param {Mapper} props.componentMapper Mapper que se propaga y se utiliza para la configuración del container
 */
export default ArenaListContainer = ({controller, filter, level=0, mode, onSelectFinish}) => {
    if(level<1) return null;

    const dispatch = useDispatch();

    const [items, setItems] = useState([]);

    const endpoint = 'http://localhost:1312/';
    useEffect(
        ()=>{
            findEntities(endpoint, controller, filter)
            .then((res) => {
                dispatch(createEntityLoad(controller,res));
                setItems(res.map(
                    (e) => ({id:e.id})
                ))
            })
        }
    ,[controller, filter])
    
    const selectionFunction = useMemo(
        ()=>createListItemSelectionFunction(mode,setItems)
    ,[mode])
    return <div>
        {
            items.map((i)=>(
                <div 
                className='arena-list-item-wrapper'
                key={i.id}
                onClick={()=>
                    selectionFunction(items, i.id)
                }>
                    <ArenaContainer
                    controller={controller}
                    id={i.id}
                    level={level}
                    mode={ArenaContainerMode.VIEW}
                    />
                </div>
            ))
        }
        {
            mode === ArenaListContainerMode.SELECT &&
                <button 
                className='arena-list-selction-finish'
                onClick={()=>{
                    onSelectFinish(items.find(i=>i.selected).id)
                }}></button>
        }
    </div>
}
/**
 * Enum de modos de renderizado de una lista
 * @readonly
 * @enum {string}
 */
export const ArenaListContainerMode = {
    SELECT: 'SELECT',
    VIEW: 'VIEW'
}