
export const createEntityRequestById = (endpoint, controller,  id) => ({
	type: types.ENTITY_REQUEST, 
	endpoint: endpoint,
	controller: controller,
	id: id
});

export const createEntityReleaseById = (controller,  id) => ({
	type: types.ENTITY_RELEASE,
	controller: controller,
	id: id
});

export const createEntityLoad = (controller, entity) => ({
	type: types.ENTITY_LOAD,
	controller: controller,
	entity: entity
})

export const createShapeLoad = (controller, shape) => ({
	type: types.SHAPE_LOAD,
	controller: controller,
	entity: shape
})

export const createEntityUpdate = (endpoint, controller, id, fieldName, value)=>({
	type: types.ENTITY_UPDATE,
	endpoint:endpoint,
	controller: controller,
	id:id,
	field: fieldName,
	value: value
})

export const types = {
	ENTITY_REQUEST: 'ENTITY_REQUEST',
	ENTITY_LOAD: 'ENTITY_LOAD',
	ENTITY_RELEASE: 'ENTITY_RELEASE',
	SHAPE_LOAD: 'SHAPE_LOAD',
	ENTITY_UPDATE: 'ENTITY_UPDATE'
}

