import React, { useMemo, useEffect } from 'react'
import { shallowEqual, useSelector, useDispatch, useStore } from 'react-redux'
import { getStorageKey } from './redux/reducer';
import ArenaField, { ArenaFieldMode } from './ArenaField';
import { createEntityRequestById, createEntityReleaseById } from './redux/actions';
import ArenaListContainer from './ArenaListContainer';
import { checkAndAddReducers } from './redux/reducersManager';
import useUpdate, { mode as useUpdateModes } from './hooks/useUpdate';
import Mapper from './mapper/ArenaComponentMapper';
import { ArenaSearchModal } from './ArenaModal';
import { ArenaListContainerMode } from './enums';

/**
 * 
 * @callback updateCallback
 * @param {number|string} newId 
 */

/**
 * 
 * @param {object} props
 * @param {string} props.controller controlador de backend para buscar las entidades
 * @param {stirng|number} props.id id de la entidad
 * @param {number} props.level nivel a utilizar
 * @param {ArenaContainerMode} props.mode modo de renderizado
 * @param {object} props.entity entidad a renderizar
 * @param {updateCallback} props.update función de actualización de la referencia (id) de este container
 * @param {Mapper} props.componentMapper Mapper que se propaga y se utiliza para la configuración del container
 */
const ArenaContainer = ({ controller, id, level, mode, entity, update, componentMapper }) => {
    // Verificación del nivel
    if (level < 1) return null;

    // redux
    const dispatch = useDispatch();
    const reduxStore = useStore();
    // redux - se agrega el reducer si no existe
    useEffect(() => {
        checkAndAddReducers(reduxStore, controller);
    }, [controller])
    // redux - dispatch del reques entity
    const endpoint = componentMapper.api;
    useEffect(() => {
        if(id){
            dispatch(createEntityRequestById(endpoint, controller, id));
            return () => {
                dispatch(createEntityReleaseById(controller, id))
            }
        }
    }, [controller, id]);



    const _shapeSelector = useMemo(() => shapeSelector(controller));
    const _entitySelector = useMemo(() => entitySelector(controller, id));
    const [shape, entityFromCentralMemory] = useSelector(
        state => [
            _shapeSelector(state),
            _entitySelector(state)
        ]
        , shallowEqual
    )
    // creación de la función de update y la entidad
    const useUpdateMode = getUseUpdateMode(mode);
    
    
    const [entityInState, updateFunctionBuilder] = useUpdate(useUpdateMode, controller, entity, id, endpoint)
    
    // si hay shape y entidad hago el render
    
    const entityToUse = getEntityToUse(mode, entityInState, entityFromCentralMemory);
    
    
    if (!shape || !entityToUse) {
        return null;
    }
    
    const commonProps = {
        updateBuilder: updateFunctionBuilder,
        shape: shape
    }
    
    switch (mode) {
        case ArenaContainerMode.SEARCH:
            return (
                <ArenaContainerSearchRender
                    {...commonProps}
                    filter={entityToUse}
                    controller={controller}
                    onSelectFinish={update} />
            )
        default:
            return (
                <ArenaContainerDefaultRender
                    {...commonProps}
                    entity={entityToUse}
                    level={level-1} 
                    mode={mode}/>
            )
    }
}

export default ArenaContainer;

/**
 *  
 * @param {object} props
 * @param {Mapper} props.componentMapper Mapper que se propaga y se utiliza para la configuración del container
 * @param {number} props.level nivel a utilizar
 * @param {ArenaContainerMode} props.mode modo de renderizado
 * @param {object} props.entity entidad a renderizar
 * @param {import('./hooks/useUpdate').updateFunctionBuilder} props.updateBuilder builder utilizado para construir las funciones de update de los componentes de cada campo
 */
export const ArenaContainerDefaultRender = ({ entity, shape, level, updateBuilder, mode, componentMapper }) => {
    return <div>
        {
            drawShape(shape, entity, level, updateBuilder, mode)
        }
    </div>
}

/**
 * @typedef ShapeField
 * @property {boolean} hidden flag que indica si es un campo oculto 
 * @property {string} controller controlador del campo
 * @property {boolean} collection flag que indica si el campo es una colección
 */


/**
 * @typedef Shape
 * @type {object}
 * @property {Object.<string,ShapeField>} fields
 */



/**
 * 
 * @param {object} props
 * @param {object} props.filter filtros iniciales para esta búsqueda
 * @param {Shape} props.shape shape para configurar el render
 * @param {string} props.controller controller de backend de esta entidad
 * @param {updateCallback} props.onSelectFinish ejecutada al terminar una selección
 * @param {import('./hooks/useUpdate').updateFunctionBuilder} props.updateBuilder builder utilizado para construir las funciones de update de los componentes de cada campo
 * @param {Mapper} props.componentMapper Mapper que se propaga y se utiliza para la configuración del container
 */
export const ArenaContainerSearchRender = ({ filter, shape, controller, onSelectFinish, updateBuilder, componentMapper }) => {
    return <div>
        <ArenaContainerDefaultRender
            entity={filter}
            shape={shape}
            level={1}
            updateBuilder={updateBuilder}
            mode={ArenaContainerMode.CREATE} />
        <ArenaListContainer
            filter={filter}
            controller={controller}
            onSelectFinish={onSelectFinish} 
            mode={ArenaListContainerMode.SELECT}
            level={2}/>
    </div>
}

/**
 * 
 * @param {Shape} shape 
 * @param {object} entity 
 * @param {number} level 
 * @param {updateFunctionBuilder} updateBuilder 
 * @param {ArenaContainerMode} mode 
 */
const drawShape = (shape, entity, level, updateBuilder, mode) => {
    return Object.keys(shape.fields).map(
        (fieldName) => {
            const field = shape.fields[fieldName];
            const update = updateBuilder(fieldName);
            const currentFieldCommonProps = {
                name: fieldName,
                key: fieldName,
                level: level,
                update: update,
                mode: mode
            }
            if (!field.hidden) {
                if (field.key) {
                    return drawKeyField(currentFieldCommonProps, field, entity[fieldName]);
                } else {
                    return drawSimpleField(currentFieldCommonProps, field, entity[fieldName]);
                }
            }
        }
    )

}

const drawSimpleField = (props, field, value) => {
    const fieldName = field.name;
    return (
        <ArenaField
            key={fieldName}
            name={fieldName}
            value={value}
            field={field}
            {...props} />
    )
}
const drawKeyField = (props, field, value) => {
    const containerCommonProps = {
        ...props,
        controller: field.controller
    }
    if (!field.collection) {
        if(containerCommonProps.level===1){
            return <ArenaKeyField
            id={value}
            {...containerCommonProps} />
        }
        else{
            return <ArenaContainer
                id={value}
                {...containerCommonProps} />
        }
    } else {
        return <ArenaListContainer
            filter={value}
            {...containerCommonProps} />
    }
}

function getEntityToUse(mode, entity, entityFromCentralMemory) {
    switch (mode) {
        case ArenaContainerMode.CREATE:
        case ArenaContainerMode.SEARCH:
            return entity;
        default:
            return entityFromCentralMemory;
    }
}

function getUseUpdateMode(mode) {
    switch (mode) {
        case ArenaContainerMode.EDIT:
            return useUpdateModes.PERSIST;
        case ArenaContainerMode.CREATE:
        case ArenaContainerMode.SEARCH:
        case ArenaContainerMode.VIEW:
        default:
                return useUpdateModes.IN_LOCAL_STATE;
    }
}

function entitySelector(controller, id) {
    return (state) => {
        const controllers = state.controller;
        const controllerState = controllers ? controllers[controller] : undefined;
        if (controllerState) {
            const storedData = controllerState[getStorageKey(id)];
            if (storedData)
                return storedData.payload;
        }
        return undefined;
    };
}

function shapeSelector(controller) {
    return (state) => {
        const controllers = state.controller;
        const controllerState = controllers ? controllers[controller] : undefined;
        if (controllerState)
            return controllerState.shape;
        else
            return undefined;
    };
}



export const ArenaKeyField = ({id, controller, mode, update})=>{
    const showSearchModal = mode === ArenaContainerMode.EDIT;
    return (
        <div>
            <ArenaContainer {...{id, controller, level:1}}/>
            {showSearchModal &&
                <ArenaSearchModal {...{onCloseModal: update, controller}}/>}
        </div>
    )
}

/**
 * Enum modos de renderizado de un container
 * @readonly
 * @enum {string}
 */
export const ArenaContainerMode = {
    ...ArenaFieldMode,
    SEARCH: 'SEARCH'
}