import { useState, useEffect } from 'react';
import { useDispatch, shallowEqual } from 'react-redux';
import { createEntityUpdate } from '../redux/actions';

/**
 * @typedef
 */
export const mode = {
    IN_LOCAL_STATE: 'IN_LOCAL_STATE',
    PERSIST: 'PERSIST'
}

/**
 * @callback updateFunctionBuilder
 * @param {string} fieldName nombre del campo que actualiza la función retornada
 */

/**
 * 
 * @param {mode} updateMode 
 * @param {string} controller 
 * @param {object} initialEntity 
 * @param {string|number} id 
 * @param {string} endpoint 
 * @returns {[object, updateFunctionBuilder]}
 */
const useUpdate = (updateMode=mode.IN_LOCAL_STATE, controller, initialEntity, id, endpoint) => {
    const persistRequired = updateMode === mode.PERSIST;
    if(persistRequired){
        if(!id) throw `Error creando función de update con id vacío: [ updateMode ${updateMode}, controller ${controller}, initialEntity ${initialEntity}, endpoint ${endpoint} ]`;
        if(!endpoint) throw `Error creando función de update con endpoint vacío: [ updateMode ${updateMode}, controller ${controller}, initialEntity ${initialEntity}, id ${id} ]`;
    }
    
    const [entity, setEntity] = useState(initialEntity);

    const dispatch = useDispatch();


    const updateFunctionBuilder = (fieldName) => (value) => {
        const chunk = {};
        chunk[fieldName] = value;
        setEntity({
            ...entity,
            ...chunk
        });
        if (persistRequired)
            dispatch(createEntityUpdate(endpoint,controller,id,fieldName, value))
    }
    return [entity, updateFunctionBuilder];
}

export default useUpdate;