import { buildControllerReducer } from "./reducer";
import { combineReducers } from "redux";
import {reducersMap} from './reducer';


export const checkAndAddReducers = (store, controller) => {
	const controllerState = store.getState().controller;
	const actionsController = controller;
	if(!actionsController || controllerState[actionsController])
		return;
	
	const reducer = {controller:{}};
	Object.entries(controllerState).forEach(([key]) => {
		reducer.controller[key] = buildControllerReducer(key);
	});
	reducer.controller[actionsController] = buildControllerReducer(actionsController);

	const controllerReducer = combineReducers(reducer.controller);

	store.replaceReducer(combineReducers({ ...reducersMap, controller: controllerReducer}));
}