import React from 'react'
import { useParams } from "react-router";  
import queryString from 'query-string';
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import ArenaContainer, { ArenaContainerMode } from './arena/ArenaContainer';
import ArenaListContainer, { ArenaListContainerMode } from './arena/ArenaListContainer';


export const ArenaApp = ()=> {
    return (

        <Switch>
                <Route 
                        path="/build/container/:mode/:controller/:id" 
                        render={(containerProps)=>(<RouteContainer {...containerProps}/>)}>
                </Route>
                {/* <Route
                        path="/build/listContainer/:mode/:controller"
                        render={(containerProps)=>(<ConectedRoutListContainer {...containerProps} componentMapper={props.componentMapper}/>)}>
                </Route> */}
        </Switch>

    )
}

const RouteContainer = (props) => {
    const { id, mode, controller } = props.match.params;
    const search = props.location.search;
    const values = queryString.parse(search);
    let shapeName = values.shapeName;
    if (!shapeName)
            shapeName = controller;
    const containerMode = getContainerMode(mode);
    return (
            <ArenaContainer
            controller={controller}
            id={Number(id)}
            mode={containerMode}
            level={2}/>
    );
}

const getContainerMode = (mode) => {
    switch (mode) {
        case 'view':
            return ArenaContainerMode.VIEW;    
        case 'edit':
            return ArenaContainerMode.EDIT;
        default:
            return ArenaContainerMode.SEARCH;
    }
}

const App = (props) => (
    <div className="container">
        <Router>
            <ArenaApp {...props}></ArenaApp>
        </Router>
    </div>
)

export default App;
