import { ArenaListContainerMode } from "../enums"


export const createListItemSelectionFunction = (mode, setItems) => {
    if(mode === ArenaListContainerMode.SELECT){
        return (items, toogledItem)=>{
            setItems(
                items.map(
                    (item)=>{
                        if(item.id!==toogledItem) 
                            return {
                                ...item,
                                selected:false
                            };
                        return {
                            ...item, 
                            selected:!item.selected
                        }
                    }
                )
            );
        }
    }
}