import { types, createEntityLoad, createShapeLoad } from "./actions";



export const backendMiddleware = (store)=>(next)=>(action) => {
	if(action.type && action.type==types.ENTITY_REQUEST){
		const {endpoint, controller, id} = action;
		fetch(`${endpoint}/${controller}/${id}`)
		.then(
			(res) => res.json())
			.then((res)=>store.dispatch(createEntityLoad(controller, res)));
		const controllerState = store.getState().controller[controller];
		if(controllerState && !controllerState.shape){
			fetch(`${endpoint}/${controller}/shape`)
			.then((res) => res.json())
				.then((res)=>
					store.dispatch(createShapeLoad(controller, res)));
		}
			
	}
	next(action);
}

export const updateEntityMiddleware = (store)=>(next)=>(action) => {
	if(action.type && action.type==types.ENTITY_UPDATE){
		const {endpoint, controller, id} = action;
		const data ={id:id};
		data[action.field]=action.value;
		fetch(`${endpoint}/${controller}`, {
			method: 'POST',
			body: JSON.stringify(data), 
			headers:{
				'Content-Type': 'application/json'
			}
		}).then(res => res.json())
			.then(response => 
				store.dispatch(createEntityLoad(controller, response)));
		return;
	}
	next(action);
}




export default backendMiddleware