import React, { useState, useMemo } from 'react'
import ReactDOM from 'react-dom';
import modal from './tools/modal'
import ArenaContainer, { ArenaContainerMode } from './ArenaContainer';

export const ArenaSearchModal = ({controller, filter = {}, onCloseModal})=>{
    
    const [portal, setPortal] = useState(null);
    function openModal(){
        const modalInstance = new modal({
            closeMethods: ['overlay', 'button', 'escape'],
            onClose: () => {
                setPortal(null);
                modalInstance.destroy();
            },
            footer: false,
            stickyFooter: false
        });
        setPortal(ReactDOM.createPortal(
            <ArenaContainer
                mode={ArenaContainerMode.SEARCH}
                controller={controller}
                entity={filter}
                update={value => {
                    onCloseModal(value);
                    modalInstance.close();
                    modalInstance.destroy();
                    setPortal(null);
                }}/>,
            modalInstance.getContent()
        ));
        modalInstance.open();
    };
    return (
        <div>
            <button onClick={() => openModal()}></button>
            {portal}
        </div>
    );
}
