const toDeep = (obj, defaultReturn, path) =>{
	if (!obj)
		return defaultReturn;
	if(!path || path.length===0)
		if(typeof obj === 'string')
			return obj;
		else
			return defaultReturn;
	return toDeep(obj[path[0]], defaultReturn, path.slice(1));	
}

/**
 * Clase para mapear componentes, traducciones y configuraciones de las aplicaciones arena
 */
export default class Mapper{
	
	/**
	 * 
	 * @param {object} controllerFieldsComponentsMap mapa de componentes por tipo del padre de un campo
	 * @param {object} templatesMap mapa de templates para conjuntos nombrados de filtros y plantillas de creación
	 * @param {Object.<string, LevelConfiguration>} componentsMap mapa de configuración de campos por nivel
	 * @param {object} screenConfigurationMap mapa de configuración de pantallas por nombre
	 * @param {Object.<string, FieldConfigurationTree>} shapeConfigurationMap mapa de configuración de shape por nombre
	 * @param {object} filtersMap mapa de filtros por nombre
	 * @param {Object.<string, LevelConfiguration>} componentsMapByType mapa de componentes por tipo de campo
 	 * @param {object} textMap mapa de traducciones
	 * @param {string} apiUrl 
	 */
	constructor(
		controllerFieldsComponentsMap={},
		templatesMap={},
		componentsMap={},
		screenConfigurationMap={default:{}},
		shapeConfigurationMap={},
		filtersMap={},
		componentsMapByType={},
		textMap={},
		apiUrl){
			if(!apiUrl)
				throw  new Error("Api url must not be undefined.");
			this.api=apiUrl;
			this.controllerFieldsComponentsMap=controllerFieldsComponentsMap;
			this.templatesMap=templatesMap;
			this.componentsMap=componentsMap;
			this.screenConfigurationMap=screenConfigurationMap;
			this.shapeConfigurationMap=shapeConfigurationMap;
			this.filtersMap=filtersMap;
			this.componentsMapByType=componentsMapByType;
			this.textMap=textMap;
		}

	/**
	 * @param {string} name nombre del campo
	 * @param {string} controller controller del campo
	 * @param {string} type type del campo
	 * @param {string} parentController tipo del padre del campo
	 * @param {number} level nivel de renderizado
	 * @returns {FieldConfiguration}
	 */
	getComponent = (name, controller, type, parentController, level) => {
		let component = {
			...this.getComponentsByType(type).default,
			...this.getComponentsByType(type)[level],
			...this.getComponents(controller).default,
			...this.getComponents(controller)[level],
			...this.getFieldComponents(parentController,name).default,
			...this.getFieldComponents(parentController,name)[level]
		};
		return component;
		
	}

	getFieldComponents = (parentController, name) => {
		let parentComponentArray = this.controllerFieldsComponentsMap[parentController];
		let fieldComponentArray = parentComponentArray ? parentComponentArray[name] : {};
		return fieldComponentArray;
	}

	getComponents = (repository) => {
		let componentArray = this.componentsMap[repository];
		return componentArray ? componentArray : {};
	}

	getComponentsByType = (type) => {
		let componentArray = this.componentsMapByType[type];
		return componentArray ? componentArray : {};
	}
	getScreenConfiguration = (name) => {
		if(!name)
			return this.screenConfigurationMap.default;
		return {...this.screenConfigurationMap.default, ...this.screenConfigurationMap[name]};
	}

	/**
	 * @param {string} name
	 * @returns {FieldConfigurationTree}
	 */
	getShapeConfiguration = (name) => {
	    return this.shapeConfigurationMap[name];
	}
	getTemplates = (controller) => {
		const templates = this.templatesMap[controller];
		return templates ? templates : {};
	}

	/**
	 * @param {string} key
	 */
	t = (key) => {
		return toDeep(this.textMap, key, key.split('.'));
	}

}

